.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #576870;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}


.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

body {
  background-color: #3a3631;
  color: #fcff67;
  padding: 40px;
  font-family: Arial;
  text-align: center;
}

h1 {
  color: #fcff67;
}

p {  
  color: #fcff67;
}

.flexbox-container {
  display: flex;
  flex-direction: row;
  justify-content: center;
  background-color: #143b3b;
  gap: 50px;
}